<template>
  <div class="page page-leaderboard">
    <h2 class="page-leaderboard__title">BITCAT leaderboard<small></small></h2>

    <span class="page-leaderboard__type-switch">
      <span @click="switchBoard('score')" class="page-leaderboard__type-switch-item" :class="{'page-leaderboard__type-switch-item_active': store.getters.getBoardType == 'score'}">BEST SCORE</span>
      <span @click="switchBoard('weight')" class="page-leaderboard__type-switch-item" :class="{'page-leaderboard__type-switch-item_active': store.getters.getBoardType == 'weight'}">CAT WEIGHT</span>
      <span @click="switchBoard('multiplayer')" class="page-leaderboard__type-switch-item" :class="{'page-leaderboard__type-switch-item_active': store.getters.getBoardType == 'multiplayer'}">MULTIPLAYER</span>
    </span>


    <div v-if="store.getters.getBoardType == 'score'" class="page-leaderboard__board">
      <table class="page-leaderboard__table">
        <tr>
          <th>No.</th>
          <th>Player</th>  
          <th>Score</th>
        </tr>
        <tr>
          <td>YOU</td>
          <td v-if="userTag">{{ userTag }}</td>
          <td v-else>Need to login</td>
          <td v-if="userTag">{{ bestScore || 0 }}₿</td>
          <td v-else>0₿</td>
        </tr>
        <tr v-for="(player, index) in leaderboardBestScore" :key="index" :class="{'page-leaderboard__table-row-im': player.userTag == userTag}">
          <td>{{ index + 1 }}</td>
          <td><router-link :to="`/player/${player.userTag}`">{{ player.userTag }}</router-link></td>
          <td>{{ player.bestScore }}₿</td>
        </tr>
      </table>

      <div class="page-leaderboard__description">INFO: BEST SCORE show to you top 25 players with best game result. You just need to play the game and PUMP the cat!</div>
    </div>


    <div v-if="store.getters.getBoardType == 'weight'" class="page-leaderboard__board">
      <table class="page-leaderboard__table">
        <tr>
          <th>No.</th>
          <th>Player</th>  
          <th>Weight</th>
        </tr>
        <tr>
          <td>YOU</td>
          <td v-if="userTag">{{ userTag }}</td>
          <td v-else>Need to login</td>
          <td v-if="userTag">{{ totalScore || 0 }}₿</td>
          <td v-else>0₿</td>
        </tr>
        <tr v-for="(player, index) in leaderboardBestWeight" :key="index" :class="{'page-leaderboard__table-row-im': player.userTag == userTag}">
          <td>{{ index + 1 }}</td>
          <td><router-link :to="`/player/${player.userTag}`">{{ player.userTag }}</router-link></td>
          <td>{{ player.totalScore }}₿</td>
        </tr>
      </table>

      <div class="page-leaderboard__description">INFO: CAT WEIGHT show top 25 players with total quantity of cat weight. You just need to play the games and PUMP the cat again and again!</div>
    </div>

    <div v-if="store.getters.getBoardType == 'multiplayer'" class="page-leaderboard__board">
      <table class="page-leaderboard__table">
        <tr>
          <th>No.</th>
          <th>Player</th>  
          <th>Wins / Losses</th>
        </tr>
        <tr>
          <td>YOU</td>
          <td v-if="userTag">{{ userTag }}</td>
          <td v-else>Need to login</td>
          <td v-if="userTag"><span class="wins">{{ totalWins || 0 }}</span> / <span class="losses">{{ totalLosses || 0 }}</span></td>
          <td v-else><span class="wins">0</span> / <span class="losses">0</span></td>
        </tr>
        <tr v-for="(player, index) in leaderboardHideSeekTopPlayers" :key="index" :class="{'page-leaderboard__table-row-im': player.userTag == userTag}">
          <td>{{ index + 1 }}</td>
          <td><router-link :to="`/player/${player.userTag}`">{{ player.userTag }}</router-link></td>
          <td><span class="wins">{{ player.wins }}</span> / <span class="losses">{{ player.losses }}</span></td>
        </tr>
      </table>

      <div class="page-leaderboard__description">INFO: MYLTIPLAYER show top 25 players with the highest total wins. You need to play Hide & Seek(PvP) and win as much as possible to get into this list.</div>
    </div>

    <img src="@/assets/coin.png" alt="COIN" class="page-leaderboard__coin">

  </div>
</template>

<script setup>
  import { useWallet } from 'solana-wallets-vue'
  import { ref, computed, onMounted } from 'vue'
  import store from '@/vuex'
  import axios from 'axios'

  const { publicKey, connected } = useWallet()
  const userTag = computed(() => store.getters.userTag)
  const bestScore = computed(() => store.getters.bestScore)
  const totalScore = computed(() => store.getters.totalScore)

  const totalWins = computed(() => store.getters.totalWins)
  const totalLosses = computed(() => store.getters.totalLosses)

  // Форматирование адреса кошелька
  const formattedPublicKey = computed(() => {
    if (!publicKey) return ''
    const key = publicKey.value.toString()
    return `${key.slice(0, 4)}...${key.slice(-4)}`
  })

  const leaderboardBestScore = ref([])
  const leaderboardBestWeight = ref([])
  const leaderboardHideSeekTopPlayers = ref([])

  onMounted(async () => {
    try {
      const response = await axios.get('/leaderboard');
      const { bestScorePlayers, bestWeightPlayers } = response.data;
      leaderboardBestScore.value = bestScorePlayers;
      leaderboardBestWeight.value = bestWeightPlayers;

      const hideSeekResponse = await axios.get('/hide-seek/top-players-stats/');
      leaderboardHideSeekTopPlayers.value = hideSeekResponse.data;
    } catch (error) {
      console.error('Error fetching leaderboard:', error);
    }
  })

  const switchBoard = (boardType) => {
    store.commit('setBoardType', {boardType})
  }
</script>

